body {
  background: #000;
  height: 200vmin;
  margin: 0;
  padding: 0;
}

.object {
  border-radius: 50%;
  margin: auto;
  position: absolute;
  inset: 0;
  transform: translateY(50vmin);
}

.p-orbit, #m-orbit {
  opacity: .5;
  background: none;
  border: .1vmin solid #d3d3d3;
}

#belt {
  width: 97vmin;
  height: 97vmin;
  margin-top: 50vmin;
  animation: 50s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

#m-orbit {
  width: 8vmin;
  height: 8vmin;
}

.planet, #moon {
  z-index: 100;
}

#sun {
  width: 12vmin;
  height: 12vmin;
}

#moon {
  background-color: #fff;
  width: 1vmin;
  height: 1vmin;
}

#mercury {
  width: 2vmin;
  height: 2vmin;
}

#venus {
  width: 3vmin;
  height: 3vmin;
}

#earth {
  width: 4vmin;
  height: 4vmin;
}

#mars {
  width: 3.5vmin;
  height: 3.5vmin;
}

#jupiter {
  width: 12vmin;
  height: 12vmin;
}

#saturn {
  border-radius: 0%;
  width: 12vmin;
  height: 12vmin;
}

#uranus {
